import _ from 'lodash'
import ol from 'openlayers'
import '@supermap/iclient-openlayers'


const RenderType = {
    SuperMapRest: 'SuperMapRest',
    SuperMapRestJsr: 'SuperMapRestJsr'
}

const getWGS84Options = () => {
    const topResolution = 360.0 / 512
    let resolutions = []
    for (let i=0; i < 22; i++) {
        resolutions.push(topResolution / (Math.pow(2, i)))
    }
    return {
        extent: [-180, -90, 180, 90],
        origin: [-180, 90],
        resolutions: resolutions,
        tileSize: 512,
        projection: 'EPSG:4326'
    }
}

const getWebMercatorOptions = () => {
    const topResolution = 2 * 6378137 * Math.PI / 512
    let resolutions = []
    for (let i=0; i < 22; i++) {
        resolutions.push(topResolution / (Math.pow(2, i)))
    }
    return {
        extent: [-180, -90, 180, 90],
        origin: [-180, 90],
        resolutions: resolutions,
        tileSize: 512
    }
}

/**
 * @param {String} renderType
 * @param {String} url
 * 首先解析url地址提取所需参数, 将属性名转大写方便提取
 * 先取避让参数，创建空图层
 * 再取投影编码和minZoom和maxZoom 计算所需的分辨率数组和tileGrid
 * 然后取sourcelayer组成真实url地址或styleUrl来生成MapBoxStyles
 * 监听style的styleloaded事件，根据以上信息创建source，并将所需信息塞给之前创建的空图层
 */
class VectorTile extends ol.Observable {
    constructor (options) {
        super()
        options = options || {}
        const renderType = options.renderType || 'SuperMapRest'
        this.selectedStyle = options.selectedStyle
        this.renderType = renderType
        this.setMap(options.map)
        const urlTemplate = options.url || ''

        this.parserUrlTemplate(urlTemplate)

        // 注册事件
        this.on('styleloaded', this.styleLoaded, this)
    }

    parserUrlTemplate (urlTemplate) {
        const params = urlTemplate.split('?')
        const formattedParams = params.length > 1 ? _.chain(params[1]).split('&').map(i => i.split('=')).fromPairs().value(): {}
        this.extractDimensions(formattedParams)
        const url = params[0] + '/'+ formattedParams['source']
        this.createVectorTile()
        this.setProjection(formattedParams.projection)
        // getResolutions(formattedParams['projection'], formattedParams.projection)
        this.setSourceLayer(formattedParams['source'])
        this.setUrl(url)
        this.setFormat(formattedParams.format)
    }

    /**
     * 提取附加参数
     */
    extractDimensions (params) {
        const keyParams = {}
        _.mapKeys(params, (v, k) => {
            keyParams[_.toUpper(k)] = v
        })
        this.keyParams = keyParams
    }

    createVectorTile () {
        const vectortile = new ol.layer.VectorTile({
            declutter: true
        })
        this.VectorTile = vectortile
    }

    setMaxResolution (res) {
        this.VectorTile.setMaxResolution(res)
    }

    setMinResolution (res) {
        this.VectorTile.setMinResolution(res)
    }

    setUrl (url) {
        this.url = url
        if (this.renderType === 'SuperMapRestJsr') {
            this.setStyleUrl()
        }
        this.setStyle()
    }

    getUrl () {
        return this.url
    }

    styleLoaded () {
        let source = null
        this.getLayer().setStyle(this.getStyle().getStyleFunction())
        this.getLayer().set('styleObj', this.getStyle())
        if (this.renderType === 'SuperMapRest') {
            source = this.getSuperMapRestSource()
            // this.setSource(this.getSuperMapRestSource())
        } else if (this.renderType === 'SuperMapRestJsr') {
            source =this.getSuperMapRestJsrSource()
            // this.setSource(this.getSuperMapRestJsrSource())
        }
        this.setSource(source)
        if (this.keyParams['STRICT']) {
            let resolutions = source.getTileGrid().getResolutions()
            let maxRes = 0
            if (!isNaN(this.keyParams['MINZOOM'])) {
                maxRes = resolutions[Number(this.keyParams['MINZOOM'])]
            } else {
                maxRes = resolutions[0]
            }
            this.VectorTile.setMaxResolution(maxRes)

            if (!isNaN(this.keyParams['MAXZOOM'])) {
                let minRes
                if (this.keyParams['MAXZOOM'] > resolutions.length - 1) {
                    minRes = resolutions[resolutions.length - 1]
                } else {
                    minRes = resolutions[this.keyParams['MAXZOOM']]
                }

                this.VectorTile.setMinResolution(minRes)
            }

        }
        this.dispatchEvent('sourceloaded')
    }

    setMap (map) {
        this.map = map
    }

    getMap () {
        return this.map
    }

    setStyleUrl () {
        this.styleUrl = this.getUrl() + '/style.json'
    }

    getStyleUrl () {
        return this.styleUrl
    }

    setStyle () {
        let style
        if (this.renderType === 'SuperMapRest') {
            style = new ol.supermap.MapboxStyles({
                url: this.getUrl(),
                source: this.getSourceLayer(),
                selectedStyle: this.selectedStyle,
                map: this.getMap()
            })
        } else if (this.renderType === 'SuperMapRestJsr') {
            style = new ol.supermap.MapboxStyles({
                style: this.getStyleUrl(),
                source: this.getSourceLayer(),
                selectedStyle: this.selectedStyle,
                map: this.getMap()
            })
        }
        style.on('styleloaded', () => {
            this.style = style
            this.dispatchEvent('styleloaded')
        })
    }

    getStyle () {
        return this.style
    }

    setProjection (projection) {
        this.projection = projection || 'EPSG:3857'
    }

    getProjection () {
        return this.projection
    }

    setFormat (formatType) {
        let format
        switch (formatType) {
            case 'MVT':
            default:
                format = new ol.format.MVT({
                    featureClass: ol.Feature
                })
                break
        }
        this.format = format
    }

    getFormat () {
        return this.format
    }

    setSourceLayer (souceLayer) {
        this.sourceLayer = souceLayer
    }

    getSourceLayer () {
        return this.sourceLayer
    }

    getTileGrid (projection) {
        let options = {}
        switch (projection) {
            case 'EPSG:4490':
            case 'EPSG:4326':
                options = getWGS84Options()
                break
            case 'EPSG:3857':
                options = getWebMercatorOptions()
                break
            default:
                options = getWebMercatorOptions()
                break
        }
        return new ol.tilegrid.TileGrid(options)
    }

    setSource (source) {
        this.getLayer().setSource(source)
    }

    getSuperMapRestSource () {
        return new ol.source.VectorTileSuperMapRest({
            format: this.getFormat(),
            tileType: 'ScaleXY',
            projection: this.getProjection(),
            url: this.getUrl(),
            tileGrid: this.getTileGrid(this.getProjection())
        })
    }

    getSuperMapRestJsrSource () {
        return new ol.source.VectorTileSuperMapRest({
            format: this.getFormat(),
            style: this.getStyleUrl(),
            source: this.getSourceLayer(),
            projection: this.getProjection(),
            tileGrid: this.getTileGrid(this.getProjection())
        })
    }

    getLayer () {
        return this.VectorTile
    }
}

export default VectorTile