import ol from 'openlayers'

/**
 * @type {string}
 */
const PROJECTION = 'projection';

/**
 * @type {string}
 */
const COORDINATE_FORMAT = 'coordinateFormat';
class MousePositionEx extends ol.control.Control{

    /**
     * @param {Options=} opt_options Mouse position options.
     */
    constructor(opt_options) {

        const options = opt_options ? opt_options : {};

        const element = document.createElement('div');
        element.className = options.className !== undefined ? options.className : 'ol-mouse-position';
        element.positionNum = options.positionNum;
        super({
            element: element,
            render: options.render || render,
            target: options.target,
            positionNum: options.positionNum
        });

        ol.events.listen(this,
            ol.Object.getChangeEventType(PROJECTION),
            this.handleProjectionChanged_, this);

        if (options.coordinateFormat) {
            this.setCoordinateFormat(options.coordinateFormat);
        }
        if (options.projection) {
            this.setProjection(options.projection);
        }

        /**
         * @private
         * @type {string}
         */
        this.undefinedHTML_ = options.undefinedHTML !== undefined ? options.undefinedHTML : '&#160;';

        /**
         * @private
         * @type {boolean}
         */
        this.renderOnMouseOut_ = !!this.undefinedHTML_;

        /**
         * @private
         * @type {string}
         */
        this.renderedHTML_ = element.innerHTML;

        /**
         * @private
         * @type {import("../proj/Projection.js").default}
         */
        this.mapProjection_ = null;

        /**
         * @private
         * @type {?import("../proj.js").TransformFunction}
         */
        this.transform_ = null;

        /**
         * @private
         * @type {import("../pixel.js").Pixel}
         */
        this.lastMouseMovePixel_ = null;

    }

    /**
     * @private
     */
    handleProjectionChanged_() {
        this.transform_ = null;
    }

    /**
     * Return the coordinate format type used to render the current position or
     * undefined.
     * @return {import("../coordinate.js").CoordinateFormat|undefined} The format to render the current
     *     position in.
     * @observable
     * @api
     */
    getCoordinateFormat() {
        return (
            /** @type {import("../coordinate.js").CoordinateFormat|undefined} */ (this.get(COORDINATE_FORMAT))
        );
    }

    /**
     * Return the projection that is used to report the mouse position.
     * @return {import("../proj/Projection.js").default|undefined} The projection to report mouse
     *     position in.
     * @observable
     * @api
     */
    getProjection() {
        return (
            /** @type {import("../proj/Projection.js").default|undefined} */ (this.get(PROJECTION))
        );
    }

    /**
     * @param {Event} event Browser event.
     * @protected
     */
    handleMouseMove(event) {
        const map = this.getMap();
        this.lastMouseMovePixel_ = map.getEventPixel(event);
        this.updateHTML_(this.lastMouseMovePixel_);
    }

    /**
     * @param {Event} event Browser event.
     * @protected
     */
    handleMouseOut(event) {
        this.updateHTML_(null);
        this.lastMouseMovePixel_ = null;
    }

    /**
     * @inheritDoc
     * @api
     */
    setMap(map) {
        super.setMap(map);
        if (map) {
            const viewport = map.getViewport();
            this.listenerKeys.push(
                ol.events.listen(viewport, ol.events.EventType.MOUSEMOVE, this.handleMouseMove, this),
                ol.events.listen(viewport, ol.events.EventType.TOUCHSTART, this.handleMouseMove, this)
            );
            if (this.renderOnMouseOut_) {
                this.listenerKeys.push(
                    ol.events.listen(viewport, ol.events.MOUSEOUT, this.handleMouseOut, this),
                    ol.events.listen(viewport, ol.events.EventType.TOUCHEND, this.handleMouseOut, this)
                );
            }
        }
    }

    /**
     * Set the coordinate format type used to render the current position.
     * @param {import("../coordinate.js").CoordinateFormat} format The format to render the current
     *     position in.
     * @observable
     * @api
     */
    setCoordinateFormat(format) {
        this.set(COORDINATE_FORMAT, format);
    }

    /**
     * Set the projection that is used to report the mouse position.
     * @param {import("../proj.js").ProjectionLike} projection The projection to report mouse
     *     position in.
     * @observable
     * @api
     */
    setProjection(projection) {
        this.set(PROJECTION, this.getProjection(projection));
    }

    /**
     * @param {?import("../pixel.js").Pixel} pixel Pixel.
     * @private
     */
    updateHTML_(pixel) {
        let html = this.undefinedHTML_;
        if (pixel && this.mapProjection_) {
            if (!this.transform_) {
                const projection = this.getProjection();
                if (projection) {
                    this.transform_ = ol.proj.getTransformFromProjections(
                        this.mapProjection_, projection);
                } else {
                    this.transform_ = ol.proj.identityTransform;
                }
            }
            const map = this.getMap();
            const coordinate = map.getCoordinateFromPixel(pixel);
            if (coordinate) {
                this.transform_(coordinate, coordinate);
                const coordinateFormat = this.getCoordinateFormat();
                if (coordinateFormat) {
                    html = coordinateFormat(coordinate);
                } else {
                    let lon = coordinate[0]
                    let lat = coordinate[1]
                    const projection = this.getProjection();
                    if (projection) {
                        if (projection.code_ === 'EPSG:4326'){
                            html = "经度：" + lon.toFixed(this.element.positionNum) +  "&nbsp;纬度：" +  lat.toFixed(this.element.positionNum);
                        } else if (projection.code_ === 'EPSG:3857') {
                            html = "经度：" + lon.toFixed(this.element.positionNum) +  "&nbsp;纬度：" +  lat.toFixed(this.element.positionNum);
                        }
                    } else {
                        if(this.mapProjection_.code_ === 'EPSG:4326'){
                            html = "经度：" + lon.toFixed(this.element.positionNum) +  "&nbsp;纬度：" +  lat.toFixed(this.element.positionNum);
                        } else if(this.mapProjection_.code_ === 'EPSG:3857') {
                            html = "经度：" + lon.toFixed(this.element.positionNum) +  "&nbsp;纬度：" +  lat.toFixed(this.element.positionNum);
                        }
                    }
                }
            }
        }
        if (!this.renderedHTML_ || html !== this.renderedHTML_) {
            this.element.innerHTML = html;
            this.renderedHTML_ = html;
        }
    }
}


/**
 * Update the projection. Rendering of the coordinates is done in
 * `handleMouseMove` and `handleMouseUp`.
 * @param {import("../MapEvent.js").default} mapEvent Map event.
 * @this {MousePosition}
 * @api
 */
export function render(mapEvent) {
    const frameState = mapEvent.frameState;
    if (!frameState) {
        this.mapProjection_ = null;
    } else {
        if (this.mapProjection_ != frameState.viewState.projection) {
            this.mapProjection_ = frameState.viewState.projection;
            this.transform_ = null;
        }
    }
}


export default MousePositionEx;
