import { sortBy } from 'lodash'
import ol from 'openlayers'

export default class Layer_Group extends ol.layer.Group {
    constructor (opt_options) {
        super(opt_options);

        this.maxLength = 0
    }

    getLayersBy (name, value) {
        if( !name || !value ) {
            return [];
        }
        let layers = this.getLayers();
        let findLayers = [];
        layers.forEach(function (eachItem) {
            if( eachItem.get(name) === value) {
                findLayers.push( eachItem );
            }
        });
        return findLayers;
    }

    removeLayer (layer) {
        let layers = this.getLayers();
        if(layer instanceof ol.layer.Base) {
            layers.remove(layer);
        }
    }

    addLayer (layer) {
        if (this.maxLength > 0 && this.getLayers().getLength() >= this.maxLength)  {
            this.dispatchEvent('overflow')
            return
        }
        this.getLayers().push(layer);
        // 图层排序, layerIndex = undefined的时候在最上层， 有layerIndex=""的时候在最下层
        let layers = sortBy(this.getLayers().getArray(), function (layer) {
            return layer.get("layerIndex");
        });
        layers = new ol.Collection(layers.slice(), {unique: true});
        this.setLayers(layers);
    }

    setMaxLength (maxLength) {
        if (isNaN(maxLength)) return
        this.maxLength = Number(maxLength)
    }

}