import  _ from 'lodash';
import ol from 'openlayers';

/**
 * WMTS服务构成所需的关键字，除此之外的皆为附加参数拼接到服务地址中
 * @type {string[]}
 * STRICT: 是否严格控制图层显示级别，默认为false， 如果为true 则按minZoom和maxZoom严格控制图层的显示级别
 */
const KEY_WORD = [
    'URL', 'LAYER', 'STYLE', 'TILEMATRIXSET', 'FORMAT', 'PROJECTION',
    'TOPRESOLUTION', 'MINZOOM', 'MAXZOOM', 'SERVICE', 'REQUEST',
    'VERSION', 'TILEMATRIX','TILEROW','TILECOL', 'CROSSORIGIN', 'WRAPX', 'EXTENT', 'ORIGIN', 'OFFSET',
    'LAYERTYPE', 'LAYERINDE', 'STRICT', 'TILE_SIZE', 'LAYER_EXTENT'
]



/**
 *  首先解析url地址提取所需参数, 提取出非必要参数作为wmts服务请求的额外参数，关键字参数转大写提取
 *  创建空图层
 *  再取投影编码和minZoom和maxZoom 计算所需的分辨率数组和tileGrid
 *  然后根据分表率数组和tileGrid创建source源
 *  将所需信息塞给空图层
 */
class WMTS extends ol.Observable {
    constructor (options) {
        super()
        options = options || {}
        // this.layerIndex = options.layerIndex
        // this.layerType = options.layerType
        this.TileLayer = new ol.layer.Tile()
        // this.TileLayer.set('layerIndex', this.layerIndex)
        // this.TileLayer.set('layerType', this.layerType)
        this.dimensions = {}
        const urlTemplate = options.url || ''
        this.parserUrlTemplate(urlTemplate)
        const source = this.createWMTSSource()
        this.TileLayer.setSource(source)
        if (this.keyParams['STRICT']) {
            let resolutions = source.getTileGrid().getResolutions()
            let maxRes = 0
            if (!isNaN(this.keyParams['MINZOOM'])) {
                maxRes = resolutions[Number(this.keyParams['MINZOOM'])]
            } else {
                maxRes = resolutions[0]

            }
            let minRes = resolutions[resolutions.length - 1]
            this.TileLayer.setMinResolution(minRes)
            this.TileLayer.setMaxResolution(maxRes)

        }
        if (this.keyParams['LAYER_EXTENT']) {
                const _extent = this.keyParams['LAYER_EXTENT'].split(',')
            const extent = _extent.map((data) => {
                return +data
            })
            this.TileLayer.setExtent(extent)
            }
    }

    parserUrlTemplate (urlTemplate) {
        const params = urlTemplate.split('?')
        const formattedParams = params.length > 1 ? _.chain(params[1]).split('&').map(i => i.split('=')).fromPairs().value(): {}
        this.setUrl(params[0])
        this.extractDimensions(formattedParams)
    }

    /**
     * 提取附加参数
     */
    extractDimensions (params) {
        const dimensions = {}
        const keyParams = {}
        _.mapKeys(params, (v, k) => {
            if (KEY_WORD.indexOf(_.toUpper(k)) === -1) {
                dimensions[k] = v
            } else {
                keyParams[_.toUpper(k)] = v
            }
        })
        this.dimensions = dimensions
        this.keyParams = keyParams
    }

    getTileGrid () {
        const projection = this.keyParams['PROJECTION'] || 'EPSG:3857'
        let topResolution, extent, origin
        switch (projection) {
            case 'EPSG:4490':
            case 'EPSG:4326':
            case 'WGS84':
                topResolution = 360.0 / 512
                extent = [-180, -90, 180, 90]
                origin = [-180, 90]
                break
            case 'EPSG:3857':
                topResolution = 2 * 6378137 * Math.PI / 512
                extent = [-20037508.3427892, -20037508.3427892, 20037508.3427892, 20037508.3427892]
                origin = [-20037508.3427892, 20037508.3427892]
                break
            default:
                topResolution = 2 * 6378137 * Math.PI / 512
                break
        }

        const resolutions = []
        const matrixIds = []
        topResolution = this.keyParams['TOPRESOLUTION'] || topResolution
        if (this.keyParams['EXTENT']) {
            const _extent = this.keyParams['EXTENT'].split(',')
            extent = _extent.map((data) => {
                return +data
            })
        }
        // extent = this.keyParams['EXTENT'] || extent
        if (this.keyParams['ORIGIN']) {
            const _origin = this.keyParams['ORIGIN'].split(',')
            origin = _origin.map((data) => {
                return +data
            })
        }
        // 最小级别为1
        const minZoom = !isNaN(this.keyParams['MINZOOM']) ? Number(this.keyParams['MINZOOM']) : 1
        const maxZoom = !isNaN(this.keyParams['MAXZOOM']) ? Number(this.keyParams['MAXZOOM']) : 18
        const offLevel = !isNaN(this.keyParams['OFFSET']) ? Number(this.keyParams['OFFSET']) : 0
        const tileSize = !isNaN(this.keyParams['TILE_SIZE']) ? Number(this.keyParams['TILE_SIZE']) : 256
        for (let i = 0; i < maxZoom; i++) {
            resolutions.push(topResolution / (Math.pow(2, i)))
            matrixIds.push(i + 1 + offLevel)
        }
        return new ol.tilegrid.WMTS({
            extent: extent,
            minZoom: minZoom,
            resolutions: resolutions,
            matrixIds: matrixIds,
            origin: origin,
            tileSize: tileSize,
        })

    }

    setUrl (url) {
        this.url = url
    }

    getUrl () {
        return this.url
    }

    createWMTSSource () {
        return new ol.source.WMTS({
            version: this.keyParams['VERSION'] || '1.0.0',
            // cacheSize: 0,
            format: this.keyParams['FORMAT'] || 'image/png',
            layer: this.keyParams['LAYER'],
            matrixSet: this.keyParams['TILEMATRIXSET'],
            style: this.keyParams['STYLE'] || 'default',
            // crossOrigin: (this.keyParams['CROSSORIGIN'] !== null && this.keyParams['CROSSORIGIN'] !== undefined) ? JSON.parse(this.keyParams['CROSSORIGIN']):true,
            crossOrigin: this.keyParams.CROSSORIGIN === 'null' ?  null : (this.keyParams.CROSSORIGIN || 'anonymous'),
            url: this.getUrl(),
            projection: this.keyParams['PROJECTION'] || 'EPSG:3857',
            wrapX: this.keyParams['WRAPX'] || false,
            dimensions: this.dimensions,
            tileGrid: this.getTileGrid()
        })
    }

    getLayer () {
        return this.TileLayer
    }
}

export default WMTS


