import ol from 'openlayers'

class Zoom extends ol.control.Control {

    /**
     * @param {Options=} opt_options Zoom options.
     */
    constructor(opt_options) {

        const options = opt_options ? opt_options : {};

        super({
            element: document.createElement('div'),
            target: options.target
        });

        // horizontal or vertical
        const align = options.align !== undefined ? options.align : 'vertical';

        const className = options.className !== undefined ? options.className : 'ol-zoom';

        const delta = options.delta !== undefined ? options.delta : 1;

        const zoomInLabel = options.zoomInLabel !== undefined ? options.zoomInLabel : '+';
        const zoomOutLabel = options.zoomOutLabel !== undefined ? options.zoomOutLabel : '\u2212';

        const zoomInTipLabel = options.zoomInTipLabel !== undefined ?
            options.zoomInTipLabel : 'Zoom in';
        const zoomOutTipLabel = options.zoomOutTipLabel !== undefined ?
            options.zoomOutTipLabel : 'Zoom out';

        const inElement = document.createElement('button');
        inElement.className = className + '-in';
        inElement.setAttribute('type', 'button');
        inElement.title = zoomInTipLabel;
        inElement.appendChild(
            typeof zoomInLabel === 'string' ? document.createTextNode(zoomInLabel) : zoomInLabel
        );

        ol.events.listen(inElement, ol.events.EventType.CLICK, this.handleClick_.bind(this, delta));

        const outElement = document.createElement('button');
        outElement.className = className + '-out';
        outElement.setAttribute('type', 'button');
        outElement.title = zoomOutTipLabel;
        outElement.appendChild(
            typeof zoomOutLabel === 'string' ? document.createTextNode(zoomOutLabel) : zoomOutLabel
        );

        ol.events.listen(outElement, ol.events.EventType.CLICK, this.handleClick_.bind(this, -delta))

        const zmElement = document.createElement('span');
        zmElement.className = className + '-level' + ' ' +  'level';

        const outInElement = document.createElement('div');
        outInElement.className = 'out-in-' + align;
        outInElement.appendChild(inElement);
        outInElement.appendChild(outElement)

        const cssClasses = className + ' ' + ol.css.CLASS_UNSELECTABLE + ' ' + ol.css.CLASS_CONTROL;
        const element = this.element;
        element.className = cssClasses;
        element.appendChild(zmElement);
        element.appendChild(outInElement);

        /**
         * @type {number}
         * @private
         */
        this.duration_ = options.duration !== undefined ? options.duration : 250;

    }

    /**
     * @param {number} delta Zoom delta.
     * @param {MouseEvent} event The event to handle
     * @private
     */
    handleClick_(delta, event) {
        event.preventDefault();
        this.zoomByDelta_(delta);
    }

    /**
     * @param {number} delta Zoom delta.
     * @private
     */
    zoomByDelta_(delta) {
        const map = this.getMap();
        const view = map.getView();
        if (!view) {
            // the map does not have a view, so we can't act
            // upon it
            return;
        }
        const currentResolution = view.getResolution();
        if (currentResolution) {
            const newResolution = view.constrainResolution(currentResolution, delta);
            if (this.duration_ > 0) {
                if (view.getAnimating()) {
                    view.cancelAnimations();
                }
                view.animate({
                    resolution: newResolution,
                    duration: this.duration_,
                    easing: ol.easing.easeOut
                });
            } else {
                view.setResolution(newResolution);
            }
        }
    }

    setMap (map) {
        map.on('moveend', this.updateZoomLevel_.bind(this))
        super.setMap(map);
    }


    updateZoomLevel_(e) {
        console.log(e, this);
        const map = e.target;
        const zoom = Math.floor((map.getView().getZoom()) * 10) / 10;
        this.element.querySelector('.level').innerText = zoom;
    }
}

export default Zoom;