import ol from 'openlayers'
import Layer_Group from './layer/Group'
import { flatMap } from 'lodash'

export default class Map extends ol.Map {
    constructor (opt) {
        opt.layers = opt.layers !== undefined ? opt.layers : []

        let baseLayerGroup = new Layer_Group({
            title: "底图图层",
            description: "底图图层, 存放layerType为0的图层"
        });
        let baseLabelLayerGroup = new Layer_Group({
            title: "底图注记图层",
            description: "底图注记图层, 存放layerType为4的图层"
        });

        let themeLayerGroup = new Layer_Group({
            title: "要素图层",
            description: "要素图层，存放layerType为1的图层,主要针对河湖库等基础水利要素和专题要素例如机井，泵站等"
        });

        let bussinessesLayerGroup = new Layer_Group({
            title: "业务图层",
            description: "业务图层，存放layerType为2的图层,主要针对ol.layer.Vector类型的处理业务逻辑的图层"
        });
        let customLayerGroup = new Layer_Group({
            title: "其他图层",
            description: "其他图层，没有layerType属性的图层"
        });
        let labelLayerGroup = new Layer_Group({
            title: "注记图层",
            description: "注记图层，没有layerType为3的图层"
        })
        opt.layers.push(baseLayerGroup, baseLabelLayerGroup, themeLayerGroup, bussinessesLayerGroup,customLayerGroup, labelLayerGroup);

        super(opt)

        this.baseLayerGroup = baseLayerGroup;
        this.baseLabelLayerGroup = baseLabelLayerGroup;
        this.themeLayerGroup = themeLayerGroup;
        this.bussinessesLayerGroup = bussinessesLayerGroup;
        this.labelLayerGroup = labelLayerGroup;
        this.customLayerGroup = customLayerGroup;
    }

    addLayer (layer) {
        let layerType = layer.get("layerType")
        // if (!layerType) {
        //     this.customLayerGroup.addLayer(layer)
        //     return
        // }
        switch (layerType) {
            case 0:
                this.baseLayerGroup.addLayer(layer)
                break
            case 4:
                this.baseLabelLayerGroup.addLayer(layer)
                break
            case 1:
                //添加要素图层
                this.themeLayerGroup.addLayer(layer)
                break
            case 2:
                //添加业务图层
                this.bussinessesLayerGroup.addLayer(layer)
                break
            case 3:
                //添加注记图层
                this.labelLayerGroup.addLayer(layer)
                break
            default:
                this.customLayerGroup.addLayer(layer)
                break
        }
    }

    addLayers (layers) {
        for (const layer of layers) {
            this.addLayer(layer)
        }
    }

    removeLayer (layer) {
        let layerType = layer.get("layerType");
        switch (layerType) {
            case 0:
                this.baseLayerGroup.getLayers().remove(layer);
                break;
            case 4:
                this.baseLabelLayerGroup.getLayers().remove(layer);
                break
            case 1:
                //移除要素图层
                this.themeLayerGroup.getLayers().remove(layer);
                break;
            case 2:
                //移除业务图层
                this.bussinessesLayerGroup.getLayers().remove(layer);
                break;
            case 3:
                //添加注记图层
                this.labelLayerGroup.getLayers().remove(layer);
                break;
            default:
                this.customLayerGroup.getLayers().remove(layer);
                break;
        }
    }

    removeLayers (layers) {
        for (const layer of layers) {
            this.removeLayer(layer)
        }
    }

    getLayersById (id) {
        let findLayers = [];
        let layers = this.getLayers();
        layers.forEach((layerItem) => {
            if (layerItem instanceof Layer_Group) {
                if (layerItem.get("id") === id) {
                    findLayers.push(layerItem);
                } else {
                    findLayers.push(layerItem.getLayersBy("id", id));
                }
            } else if (layerItem instanceof ol.layer.Base) {
                if (layerItem.get("id") === id) {
                    findLayers.push(layerItem);
                }
            }
        });
        return flatMap(findLayers);
    }

}